"use client";

import { NEWS } from "@/app/data/data";
import Image from "next/image";
import Link from "next/link";
import { Facebook, Linkedin, Twitter, LinkIcon } from "lucide-react";

type Params = {
  params: {
    slug: string;
  };
};

function generateSlug(title: string) {
  return title
    .toLowerCase()
    .replace(/[^a-z0-9]+/g, "-")
    .replace(/^-+|-+$/g, "");
}

export default function NewsDetailPage({ params }: Params) {
  const { slug } = params;

  // البحث عن الخبر المناسب بالـ slug
  const newsItem = NEWS.find((n) => generateSlug(n.title) === slug);

  if (!newsItem) {
    return (
      <main className="w-full bg-white text-[#1e1e1e] px-6 sm:px-8 lg:px-[70px] py-16">
        <div className="text-center">
          <h1 className="font-editors text-2xl mb-4">News not found</h1>
          <Link
            href="/news"
            className="inline-block bg-[#e4dedd] text-black px-6 py-2 rounded-full font-helvetica text-[14px] hover:bg-[#d9cfcf] transition-colors"
          >
            Back to News
          </Link>
        </div>
      </main>
    );
  }

  return (
    <main className="w-full bg-white text-[#1e1e1e]">
      {/* Header Navigation */}
      <div className="px-6 sm:px-8 lg:px-[70px] py-6 border-b border-black/10">
        <Link
          href="/news"
          className="inline-flex items-center text-[14px] font-helvetica text-black/60 hover:text-black transition-colors"
        >
          ← Back to News
        </Link>
      </div>

      {/* Article Content */}
      <article className="px-6 sm:px-8 lg:px-[70px] py-16 lg:py-24">
        {/* Article Header */}
        <header className="mb-12 lg:mb-16">
          <div className="text-[12px] text-black/60 mb-4 font-helvetica">
            {newsItem.date}
          </div>

          <h1 className="font-editors text-[28px] sm:text-[36px] lg:text-[44px] leading-tight mb-4 max-w-4xl">
            {newsItem.title}
          </h1>

          {newsItem.subtitle && (
            <h2 className="font-editors italic text-[22px] sm:text-[28px] lg:text-[34px] leading-snug text-black/90 max-w-4xl">
              {newsItem.subtitle}
            </h2>
          )}
        </header>

        {/* Featured Image */}
        <div className="mb-12 lg:mb-16">
          <div className="relative w-full h-[300px] sm:h-[400px] lg:h-[500px] max-w-6xl mx-auto">
            <Image
              src={newsItem.image || "/placeholder.svg"}
              alt={newsItem.title}
              fill
              className="object-cover rounded-lg"
              priority
            />
          </div>
        </div>

        {/* Article Body */}
        <div className="max-w-4xl mx-auto">
          {/* Main Content */}
          <div className="prose prose-lg max-w-none">
            {newsItem.body ? (
              <div className="space-y-6">
                {newsItem.body.split("\n\n").map((paragraph, index) => (
                  <p
                    key={index}
                    className={`font-helvetica text-[16px] lg:text-[18px] leading-relaxed text-black/90 ${
                      index === 0
                        ? "first-letter:text-5xl first-letter:font-editors first-letter:float-left first-letter:mr-3 first-letter:mt-1"
                        : ""
                    }`}
                  >
                    {paragraph}
                  </p>
                ))}
              </div>
            ) : (
              <>
                <p className="font-helvetica text-[16px] lg:text-[18px] leading-relaxed text-black/90 mb-8 first-letter:text-5xl first-letter:font-editors first-letter:float-left first-letter:mr-3 first-letter:mt-1">
                  {newsItem.excerpt}
                </p>

                {newsItem.excerpt2 && (
                  <p className="font-helvetica text-[16px] lg:text-[18px] leading-relaxed text-black/90 mb-8">
                    {newsItem.excerpt2}
                  </p>
                )}

                {/* Additional Content Sections */}
                <div className="space-y-8 mt-12">
                  <div>
                    <h3 className="font-editors text-[22px] lg:text-[26px] mb-4">
                      Project Details
                    </h3>
                    <p className="font-helvetica text-[16px] lg:text-[18px] leading-relaxed text-black/90">
                      The Four Seasons Hotel and Private Residences Jeddah at
                      the Corniche represents a significant milestone in luxury
                      hospitality development in Saudi Arabia. This coastal
                      retreat will feature world-class amenities and services
                      that Four Seasons is renowned for globally.
                    </p>
                  </div>

                  <div>
                    <h3 className="font-editors text-[22px] lg:text-[26px] mb-4">
                      Strategic Partnership
                    </h3>
                    <p className="font-helvetica text-[16px] lg:text-[18px] leading-relaxed text-black/90">
                      The collaboration between Four Seasons Hotels and Resorts
                      and Midad Real Estate demonstrates the growing confidence
                      in Saudi Arabia's tourism and hospitality sector. This
                      partnership combines Four Seasons' operational excellence
                      with Midad's local expertise and market knowledge.
                    </p>
                  </div>

                  <div>
                    <h3 className="font-editors text-[22px] lg:text-[26px] mb-4">
                      Future Outlook
                    </h3>
                    <p className="font-helvetica text-[16px] lg:text-[18px] leading-relaxed text-black/90">
                      This development aligns with Saudi Arabia's Vision 2030
                      initiative to diversify the economy and establish the
                      Kingdom as a premier global destination for business and
                      leisure travelers. The project is expected to set new
                      standards for luxury hospitality in the region.
                    </p>
                  </div>
                </div>
              </>
            )}
          </div>

          {/* Social Sharing */}
          <div className="mt-16 pt-8 border-t border-black/10">
            <div className="mb-8">
              <h3 className="font-editors text-[18px] mb-4 italic">
                Share Article
              </h3>
              <div className="flex gap-4">
                <button
                  onClick={() =>
                    window.open(
                      `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(
                        window.location.href
                      )}`,
                      "_blank"
                    )
                  }
                  className="w-8 h-8 flex items-center justify-center text-black hover:text-blue-600 transition-colors"
                  aria-label="Share on Facebook"
                >
                  <Facebook size={20} />
                </button>
                <button
                  onClick={() =>
                    window.open(
                      `https://www.linkedin.com/sharing/share-offsite/?url=${encodeURIComponent(
                        window.location.href
                      )}`,
                      "_blank"
                    )
                  }
                  className="w-8 h-8 flex items-center justify-center text-black hover:text-blue-700 transition-colors"
                  aria-label="Share on LinkedIn"
                >
                  <Linkedin size={20} />
                </button>
                <button
                  onClick={() =>
                    window.open(
                      `https://twitter.com/intent/tweet?url=${encodeURIComponent(
                        window.location.href
                      )}&text=${encodeURIComponent(newsItem.title)}`,
                      "_blank"
                    )
                  }
                  className="w-8 h-8 flex items-center justify-center text-black hover:text-gray-600 transition-colors"
                  aria-label="Share on X (Twitter)"
                >
                  <Twitter size={20} />
                </button>
                <button
                  onClick={() =>
                    navigator.clipboard.writeText(window.location.href)
                  }
                  className="w-8 h-8 flex items-center justify-center text-black hover:text-gray-600 transition-colors"
                  aria-label="Copy link"
                >
                  <LinkIcon size={20} />
                </button>
              </div>
            </div>

            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
              <div className="text-[12px] text-black/60 font-helvetica">
                Published on {newsItem.date}
              </div>
              <Link
                href="/news"
                className="bg-[#e4dedd] text-black px-6 py-2 rounded-full font-helvetica text-[14px] hover:bg-[#d9cfcf] transition-colors"
              >
                More News
              </Link>
            </div>
          </div>
        </div>
      </article>

      {/* Related Articles */}
      <section className="bg-white px-6 sm:px-8 lg:px-[70px] py-16">
        <div className="max-w-6xl mx-auto">
          <div className="flex justify-between items-center mb-12">
            <h2 className="font-editors text-[32px] lg:text-[40px]">
              Related Articles
            </h2>
            <Link
              href="/news"
              className="text-[14px] font-helvetica text-black/60 hover:text-black transition-colors"
            >
              View All
            </Link>
          </div>

          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 lg:gap-12">
            {NEWS.filter((n) => n.id !== newsItem.id)
              .slice(0, 2)
              .map((relatedNews) => (
                <article key={relatedNews.id} className="group">
                  <Link
                    href={`/news/${generateSlug(relatedNews.title)}`}
                    className="block"
                  >
                    {/* Image */}
                    <div className="relative h-64 sm:h-80 lg:h-96 mb-6 overflow-hidden">
                      <Image
                        src={relatedNews.image || "/placeholder.svg"}
                        alt={relatedNews.title}
                        fill
                        className="object-cover group-hover:scale-105 transition-transform duration-300"
                      />
                    </div>

                    {/* Content below image - matching exact design */}
                    <div className="space-y-3">
                      {/* Date */}
                      <div className="text-[12px] text-black/60 font-helvetica">
                        {relatedNews.date}
                      </div>

                      {/* Title */}
                      <h3 className="font-editors text-[20px] sm:text-[24px] lg:text-[28px] leading-tight">
                        {relatedNews.title}
                        {relatedNews.subtitle && (
                          <span className="block font-editors italic text-[18px] sm:text-[20px] lg:text-[24px] text-black/80 mt-1">
                            {relatedNews.subtitle}
                          </span>
                        )}
                      </h3>

                      {/* Divider line */}
                      <div className="w-12 h-px bg-black/20 my-4"></div>

                      {/* Description */}
                      <p className="font-helvetica text-[14px] lg:text-[16px] leading-relaxed text-black/70 line-clamp-4">
                        {relatedNews.excerpt}
                      </p>

                      {/* Read more button */}
                      <div className="pt-4">
                        <span className="inline-block bg-[#e4dedd] text-black px-6 py-3 font-helvetica text-[14px] hover:bg-[#d9cfcf] transition-colors group-hover:bg-black group-hover:text-white">
                          Read more
                        </span>
                      </div>
                    </div>
                  </Link>
                </article>
              ))}
          </div>
        </div>
      </section>
    </main>
  );
}
